/**
 * 
 * @param {Object} options
 * @param {String} options.name 
 * @param {Object} options.data 
 * @param {Boolean} options.showLoading 
 * @param {Boolean} options.useMock 
 */
function request(options = {}) {
  const { name, data = {}, showLoading = true, useMock = false } = options;
  
  // 
  if (!name) {
    console.error('');
    return Promise.reject(new Error(''));
  }
  
  if (showLoading) {
    uni.showLoading({
      title: '...',
      mask: true
    });
  }
  
  // 
  const systemInfo = uni.getSystemInfoSync();
  const isGuestMode = systemInfo && systemInfo.result && systemInfo.result.isGuest || false;
  
  // 
  // 1. 
  // 2. 
  // 3. 
  const shouldUseMockData = useMock || isGuestMode || (getApp() && getApp().globalData && getApp().globalData.$isDevMode) || false;
  
  if (shouldUseMockData) {
    console.log(`[] : ${name}`);
  }
  
  return new Promise((resolve, reject) => {
    // 
    if (shouldUseMockData) {
      console.warn(':', useMock ? '' : (isGuestMode ? '' : ''));
      // 
      setTimeout(() => {
        if (showLoading) {
          uni.hideLoading();
        }
        
        // 
        let mockData = getMockData(name, data);
        
        resolve(mockData);
      }, 500);
      return;
    }
    
    //  - uniCloud
    // 
    const spaceId = getApp().globalData.$spaceId || 'mp-d0c06b27-ec33-40fe-b28b-337811bd2f29';
    
    //  - UniCloud

    console.log(`[${name}]UniCloudspaceId: ${spaceId}`);
    // 
    if (typeof wx !== 'undefined' && wx.cloud) {
      wx.cloud = null;
      console.log('request.js');
    }

    
    console.log(`[${name}]ID: ${spaceId}`);
    
    // uniCloud
    if (typeof uniCloud === 'undefined') {
      console.error('uniCloud');
      // 
      let mockData = getMockData(name, data);
      if (mockData) {
        if (showLoading) {
          uni.hideLoading();
        }
        resolve(mockData);
        return;
      }
    }
    
    // uniCloud
    uniCloud.callFunction({
      name,
      data,
      success: res => {
        console.log(` ${name} :`, res.result);
        
        // 
        if (!res.result) {
          console.warn(` ${name} `);
          // 
          let mockData = getMockData(name, data);
          return resolve(mockData);
        }
        
        // mongo(mongo_cell_decision_not_found)
        if (res.result.code === -1 && 
            res.result.message && 
            (res.result.message.includes('mongo_cell_decision_not_found') || 
             res.result.message.includes('') && res.result.message.includes(''))) {
          console.warn(` ${name} :`, res.result.message);
          // 
          uni.showToast({
            title: '',
            icon: 'none',
            duration: 2000
          });
          // 
          let mockData = getMockData(name, data);
          return resolve(mockData);
        }
        
        resolve(res.result);
      },
      fail: err => {
        console.error(` ${name} :`, err);
        
        // 
        console.warn(` ${name} `);
        
        // 
        let mockData = getMockData(name, data);
        
        if (mockData) {
          return resolve(mockData);
        }
        
        uni.showToast({
          title: '',
          icon: 'none'
        });
        reject(err);
      },
      complete: () => {
        if (showLoading) {
          uni.hideLoading();
        }
      }
    });
  });
}

