import request from './request.js'

// URL
const baseURL = ''

// 
const debugAPI = (name, result) => {
  console.log(`API: ${name}`, result);
  return result;
}

// API
const courseApi = {
  // 
  getCourseList(data, useMock = false) {
    console.log('getCourseList:', data);
    return request({
      name: 'getCourseList',
      data,
      useMock
    }).then(res => debugAPI('getCourseList', res));
  },
  
  // 
  getCourseDetail(id, useMock = false) {
    console.log('getCourseDetailID:', id);
    return request({
      name: 'getCourseDetail',
      data: { id },
      useMock
    }).then(res => debugAPI('getCourseDetail', res));
  },
  
  // 
  getNewsList(data = {}, useMock = false) {
    console.log('getNewsList:', data);
    return request({
      name: 'getNewsList',
      data,
      useMock
    }).then(res => debugAPI('getNewsList', res));
  },
  
  // 
  getRecommendCourses(data = {}, useMock = false) {
    console.log('getRecommendCourses:', data);
    return request({
      name: 'getRecommendCourses',
      data,
      useMock
    }).then(res => debugAPI('getRecommendCourses', res));
  },
  
  // 
  bookCourse(data, useMock = false) {
    return request({
      name: 'bookCourse',
      data,
      useMock
    });
  }
}

